
.arrow-container[data-v-0c5fa0c0] {
  width: 60px;
  height: 60px;
  top: calc(50vh - 30px);
  -webkit-transition: .25s opacity ease;
  transition: .25s opacity ease;
  border-radius: 50%;
  -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  opacity: .8;
  cursor: pointer;
}
.arrow-container[data-v-0c5fa0c0]:hover {
    opacity: 1;
}
.arrow-container .circle[data-v-0c5fa0c0] {
    fill: #7D4896;
}
.arrow-container .arrow-path[data-v-0c5fa0c0] {
    fill: none;
    stroke: #A0A0A0;
    stroke-width: 3px;
    stroke-linecap: round;
    stroke-linejoin: round;
}
.arrow-container .arrow-path.animate[data-v-0c5fa0c0] {
      -webkit-transition: .4s all ease;
      transition: .4s all ease;
      stroke: #F7F9FF;
      stroke-dasharray: 0 140;
      stroke-dashoffset: 2;
}
.arrow-container:hover .arrow-path.animate[data-v-0c5fa0c0] {
    stroke-dasharray: 70 0;
}



